import React from 'react';

import { MoviesFromServer } from '../../type/MoviesFromServer';

import './Sidebar.scss';

type Props = {
  movies: MoviesFromServer[]
};

export const Sidebar: React.FC<Props> = ({ movies }) => (
  <aside className="sidebar">
    <h2>
      <strong>
        Movies
      </strong>
    </h2>
    <ul>
      {movies.map(movie => (
        <li key={movie.imdbId}>
          <a
            href={movie.imdbUrl}
            target="_blank"
            rel="noreferrer"
          >
            {movie.title}
          </a>
        </li>
      ))}
    </ul>
  </aside>
);
